@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    /* color: #000; */
    font-family: Poppins,sans-serif;
    font-weight: 400;
}
.logo{
    list-style: none;
}
.navbar{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 75px;
    background-color: #FFFFFF;
}
li{
    padding: 0 15px;
}
.logo{
    font-size: 30px;
}
.nav-links{
    display: flex;
    /* justify-content: flex-end; */
    list-style: none;
    width: 50%;
}
a{
    color: #555252;
    display: inline-block;
    font-size: 15px;
    font-weight: 500;
    letter-spacing: .8px;
    line-height: 94px;
    text-decoration: none;
    font-family: Poppins;
    font-weight: 500;

}
.mobile-menu-icon{
    display: none;
}
@media screen and (max-width:780px){
    .nav-links{
        display: none;
    }
    .nav-links-mobile{
        position: absolute;
        display: block;
        list-style: none;
        background-color: #005469;
        left: 0;
        top: 75px;
        transition: all 0.5s ease-out;
        width: 100%;
    }
    a{
        text-align: center;
        padding: 32px;
        width: 100%;
        transition: all 0.5s ease;
    }
    .signup{
        border: none;
    }
    .mobile-menu-icon{
        display: block;
        position: absolute;
        font-size: 30px;
        /* color: white;
        background-color: #00828c; */
        border: none;
        outline: none;
        top: 15px;
        right: 25px;
        background-color: transparent;
    }
    .logo {
        font-size: 30px;
        position: absolute;
        top: 11px;
        left: 30px;
    }
}